import homePage from '@/components/homePage'

export default {
  name: 'App',
  components: {
    mainMenu, projectsPage, skillsPage, homePage
  }
}
</script>

<style>
#app {
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 15px;
  max-width: 1000px;
  margin: 0 auto;
}
</style>



